import React, { useEffect, useState } from "react";
import Form from "../Form/Form";
import CardCita from "../Cita/Card-Cita";
import { db } from "../Services/firebase";
import "./pag.css";
const Pag = () => {
  const [Citas, setCitas] = useState([]);
  const [Loading, setLoading] = useState(false);
  //agrego las citas a la bd
  const agregarCita = async (Datos) => {
    await db.collection("Citas").doc().set(Datos);
  };
  //obtengo los Datos de la bd
  const ObetenerDatos = async () => {
    db.collection("Citas").onSnapshot((datos) => {
      const CitasTotales = [];
      //Recorro la BD
      datos.forEach((doc) => {
        CitasTotales.push({ ...doc.data(), id: doc.id });
      });
      setCitas(CitasTotales);
      setLoading(true);
    });
  };
  const deleteCita = async (id) => {
    await db
      .collection("Citas")
      .doc(`${id}`)
      .delete()
      .then(() => {})
      .catch((error) => {
        console.error("Error removing document: ", error);
      });
  };
  //Lo uso una sola vez
  useEffect(() => {
    ObetenerDatos();
  }, []);

  if (Loading === false) {
    return <h1>Loading...</h1>;
  }
  if (Citas.length === 0) {
    return (
      <>
        <div className="container">
          <h1>Crear Cita</h1>
          <Form addOrEdit={agregarCita} />
        </div>
        <div className="contenedor-citas">
          <h1>Aun no se agendo ninguna cita</h1>
        </div>
      </>
    );
  }
  return (
    <>
      <div className="container">
        <h1>Crear Cita</h1>
        <Form addOrEdit={agregarCita} />
      </div>

      <div className="contenedor-citas">
        <h1>Administrar Citas</h1>
        {Citas.map((cita) => (
          <li key={cita.id} style={{ listStyle: "none" }}>
            <CardCita props={cita} Delete={deleteCita} />
          </li>
        ))}
      </div>
    </>
  );
};

export default Pag;
