import "./CardCita.css";
function CardCita(props) {
  return (
    <div className="Contenedor-Card">
      <h3>Mascota:{props.props.Mascota}</h3>
      <h3>Dueño:{props.props.Dueño}</h3>
      <h3>Hora:{props.props.Fecha}</h3>
      <h3>Hora:{props.props.Hora}</h3>
      <h3>Sintomas:{props.props.Sintomas}</h3>
      <div className="button">
        <button onClick={() => props.Delete(props.props.id)}>Eliminar</button>
      </div>
    </div>
  );
}
export default CardCita;
