import "./Form.css";
import React, { useState } from "react";

const Form = (props) => {
  const values = {
    Mascota: "",
    Dueño: "",
    Fecha: "",
    Hora: "",
    Sintomas: "",
  };
  const [form, setForm] = useState(values);
  const handleChange = (e) => {
    const { name, value } = e.target;
    setForm({ ...form, [name]: value });
  };

  const Subir = (e) => {
    e.preventDefault();
    props.addOrEdit(form);
  };

  return (
    <>
      <form className="Form" onSubmit={Subir}>
        <span>Nombre Mascota</span>
        <input
          placeholder="Nombre Mascota"
          name="Mascota"
          onChange={handleChange}
          required
        />
        <span>Nombre Dueño</span>
        <input
          placeholder="Nombre Dueño"
          name="Dueño"
          onChange={handleChange}
          required
        />
        <span>Fecha</span>
        <input type="time" name="Fecha" onChange={handleChange} required />
        <span>Hora</span>
        <input type="date" name="Hora" onChange={handleChange} required />
        <span>Sintomas</span>
        <textarea name="Sintomas" onChange={handleChange} required />
        <button type="submit">Agregar Cita</button>
      </form>
    </>
  );
};
export default Form;
